.events{
    background-color: white; 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
     max-height: 400px;
    margin: 10px;
    padding: 10px;
    width: 100%; 
     z-index: 1; 
     box-shadow: 3px 5px 3px lightgray;

}

.event-info{
    height: 100px;
    margin-bottom: 10px;
    margin-top: 1rem;

}

.events > img{
    max-height: 220px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px; 
    margin-top: -4rem;
}

